import React, { useState } from "react";
import "semantic-ui-css/semantic.css";
import {
  Dropdown,
  Container,
  Header,
  Table,
  Button,
  Menu,
  Segment,
  Icon,
} from "semantic-ui-react";
import data from "./pf";
import data2 from "./pf2";
import data3 from "./pf3";
import ata from "./ata";
import CsvDownload from "react-json-to-csv";

class modelo {
  constructor(
    name,
    comp1,
    comp2,
    blu,
    inch,
    price,
    price1,
    ata1,
    price2,
    ata2
  ) {
    this.name = name;
    this.comp1 = comp1;
    this.comp2 = comp2;
    this.blu = blu;
    this.inch = inch;
    this.price = price;
    this.price1 = price1;
    this.ata1 = ata1;
    this.price2 = price2;
    this.ata2 = ata2;
  }
}

class simpleModel {
  constructor(model, marca, cat, subcat, spec, offer, tag) {
    this.model = model;
    this.marca = marca;
    this.cat = cat;
    this.subcat = subcat;
    this.spec = spec;
    this.offer = offer;
    this.tag = tag;
  }
}

class listaModelo {
  constructor(key, value, text) {
    this.key = key;
    this.value = value;
    this.text = text;
  }
}

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}

function App() {
  let lgOLED = 0;
  let ssOLED = 0;
  let snOLED = 0;
  let lgNano = 0;
  let ssQled = 0;
  let snSUHD = 0;
  let lgUHD = 0;
  let ssUHD = 0;
  let snUHD = 0;
  let lgFHD = 0;
  let ssFHD = 0;
  let snFHD = 0;
  const [cat, setCat] = useState("All");
  const [marca, setMarca] = useState([]);
  let [opcion1, setOpcion1] = useState("");
  let [opcion2, setOpcion2] = useState("");
  Object.keys(data).map((item) => {
    if (data[item].cat_1 === "LG" && data[item].cat_5 === "OLED") {
      lgOLED++;
    } else if (data[item].cat_1 === "SONY" && data[item].cat_5 === "OLED") {
      snOLED++;
    } else if (data[item].cat_1 === "LG" && data[item].cat_5 === "NanoCell") {
      lgNano++;
    } else if (data[item].cat_1 === "SAMSUNG" && data[item].cat_5 === "QLED") {
      ssQled++;
    } else if (
      data[item].cat_1 === "SAMSUNG" &&
      data[item].cat_6 === "UHD 4K"
    ) {
      ssUHD++;
    } else if (data[item].cat_1 === "LG" && data[item].cat_6 === "UHD 4K") {
      lgUHD++;
    } else if (data[item].cat_1 === "SONY" && data[item].cat_6 === "UHD 4K") {
      snUHD++;
    } else if (
      data[item].cat_1 === "SAMSUNG" &&
      (data[item].cat_6 === "FULL HD" || data[item].cat_6 === "HD")
    ) {
      ssFHD++;
    } else if (
      data[item].cat_1 === "LG" &&
      (data[item].cat_6 === "FULL HD" || data[item].cat_6 === "HD")
    ) {
      lgFHD++;
    } else if (
      (data[item].cat_1 === "SONY" && data[item].cat_6 === "FULL HD") ||
      data[item].cat_6 === "HD"
    ) {
      snFHD++;
    }
  });
  let tabla1 = Object.keys(data3).map((item) => {
    return (data3[item].Category === cat || cat === "All") &&
      (marca.includes(data3[item].cat_1) ||
        marca.includes("All") ||
        marca.length === 0) ? (
      <Table.Row>
        <Table.Cell>{data3[item].cat_1}</Table.Cell>
        <Table.Cell>{data3[item].SubCategory}</Table.Cell>
        <Table.Cell
          selectable
          onClick={(e) => {
            if (opcion1 === "") {
              setOpcion1(e.target.innerText);
            } else {
              setOpcion2(e.target.innerText);
            }
          }}
        >
          {data3[item].cat_2}
        </Table.Cell>
        <Table.Cell>{data3[item].Specs}</Table.Cell>
        <Table.Cell>{currencyFormat(data3[item].Offer)}</Table.Cell>
        <Table.Cell>{currencyFormat(data3[item].Tag)}</Table.Cell>
        <Table.Cell>
          {currencyFormat(data3[item].Tag - data3[item].Offer)}
        </Table.Cell>
      </Table.Row>
    ) : null;
  });
  let listado = [];
  let listaModel = Object.keys(data3).map((item) => {
    if (
      (data3[item].Category === cat || cat === "All") &&
      (marca.includes(data3[item].cat_1) ||
        marca.includes("All") ||
        marca.length === 0)
    ) {
      return listado.push(
        new listaModelo(data3[item].cat_2, data3[item].cat_2, data3[item].cat_2)
      );
    }
  });
  let atarows = ata.map((item, i) => {
    return item["PROMO LG"] === "" && item["PROMO COMP"] === "" ? null : (
      <Table.Row>
        <Table.Cell>{item.PL1}</Table.Cell>
        <Table.Cell>{item["PRODUCT"]}</Table.Cell>
        <Table.Cell>{item["SIZE"]}</Table.Cell>
        <Table.Cell>{item["YEAR"]}</Table.Cell>
        <Table.Cell>{item["COMPETITOR"]}</Table.Cell>
        <Table.Cell>{item["YEAR.1"]}</Table.Cell>
        <Table.Cell textAlign="right">{item["PROMO LG"]}</Table.Cell>
        <Table.Cell textAlign="right">{item["TAG LG"]}</Table.Cell>
        <Table.Cell textAlign="right">{item["PROMO COMP"]}</Table.Cell>
        <Table.Cell textAlign="right">{item["TAG COMP"]}</Table.Cell>
        <Table.Cell textAlign="right">
          {item["ATA"] !== ""
            ? parseFloat(item["ATA"] * 100).toFixed(2) + "%"
            : ""}
        </Table.Cell>
        <Table.Cell textAlign="left">
          {item["ATA"] > 1.05 ? (
            <Icon name="exclamation circle" color="red" size="large"></Icon>
          ) : null}
        </Table.Cell>
      </Table.Row>
    );
  });

  let unique = data2.filter((item) => {
    if (!cat) return true;
    if (item.Category === cat) {
      return true;
    }
  });
  let unique2 = [...new Set(unique.map((item) => item.cat_1))];
  let pl2 = [...new Set(data2.map((item) => item.Category))];
  let newdata = unique.filter((item) => {
    if (!marca) return true;
    if (
      marca.includes(item.cat_1) ||
      marca.includes("All") ||
      marca.length === 0
    ) {
      return true;
    }
  });
  let pl3 = [...new Set(newdata.map((item) => item.SubCategory))];
  const [activo, setActivo] = useState("Precio");
  const handleItemClick = (e, { name }) => {
    setActivo(name);
  };
  return (
    <Container style={{ marginTop: "3em" }}>
      <Header as="h1" color="red">
        PANAFOTO ONLINE DATA
      </Header>
      <Header as="h2">
        <Menu inverted color="red">
          <Menu.Item
            name="Precio"
            active={activo === "Precio"}
            onClick={handleItemClick}
          ></Menu.Item>
          <Menu.Item
            name="SKU"
            active={activo === "SKU"}
            onClick={handleItemClick}
          ></Menu.Item>
          <Menu.Item
            name="ATA"
            active={activo === "ATA"}
            onClick={handleItemClick}
          ></Menu.Item>
        </Menu>
      </Header>
      {activo === "Precio" || activo === "SKU" ? (
        <Dropdown
          button
          search
          selection
          closeOnChange
          options={pl2.map((item) => {
            return { key: item, text: item, value: item };
          })}
          placeholder="Categorias"
          value={cat}
          onChange={(e, { value }) => {
            setCat(value);
            setOpcion1("");
            setOpcion2("");
            setMarca([]);
          }}
        />
      ) : null}

      {activo === "Precio" ? (
        <Dropdown
          button
          multiple
          search
          selection
          closeOnChange
          options={unique2.map((item) => {
            return { key: item, text: item, value: item };
          })}
          placeholder="Marca"
          value={marca}
          onChange={(e, { value }) => {
            setMarca(value);
            console.log(value);
          }}
        />
      ) : null}

      {activo === "Precio" ? (
        <Dropdown
          clearable
          search
          selection
          value={opcion1}
          options={listado}
          onChange={(e, { value }) => {
            setOpcion1(value);
            console.log(value);
          }}
          placeholder="Seleccionar Modelo"
        />
      ) : null}

      {activo === "Precio" ? (
        <Dropdown
          clearable
          search
          selection
          value={opcion2}
          options={listado}
          onChange={(e, { value }) => {
            setOpcion2(value);
            console.log(value);
          }}
          placeholder="Seleccionar Modelo"
        />
      ) : null}

      {opcion1 !== "" && opcion2 !== "" ? (
        <Button
          onClick={() => {
            console.log(opcion1, opcion2);
          }}
        >
          Comparar
        </Button>
      ) : null}
      <Segment>
        {activo === "Precio" ? (
          <Table color="red" celled>
            <Table.Header>
              <Table.HeaderCell>Marca</Table.HeaderCell>
              <Table.HeaderCell>Sub Category</Table.HeaderCell>
              <Table.HeaderCell>Modelo</Table.HeaderCell>
              <Table.HeaderCell>Sepcs</Table.HeaderCell>
              <Table.HeaderCell>Offer Price</Table.HeaderCell>
              <Table.HeaderCell>Tag Price</Table.HeaderCell>
              <Table.HeaderCell>Discount</Table.HeaderCell>
            </Table.Header>
            <Table.Body>{tabla1}</Table.Body>
          </Table>
        ) : null}
        {activo === "SKU" ? (
          <Table color="red" collapsing>
            <Table.Header>
              <Table.HeaderCell>PG1</Table.HeaderCell>
              <Table.HeaderCell>PL1</Table.HeaderCell>
              <Table.HeaderCell>LG</Table.HeaderCell>
              <Table.HeaderCell>SS</Table.HeaderCell>
              <Table.HeaderCell>Sony</Table.HeaderCell>
            </Table.Header>
            <Table.Body>
              <Table.Row>
                <Table.Cell>LTV</Table.Cell>
                <Table.Cell>OLED</Table.Cell>
                <Table.Cell>{lgOLED}</Table.Cell>
                <Table.Cell>{ssOLED}</Table.Cell>
                <Table.Cell>{snOLED}</Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>LTV</Table.Cell>
                <Table.Cell>NANO / QLED</Table.Cell>
                <Table.Cell>{lgNano}</Table.Cell>
                <Table.Cell>{ssQled}</Table.Cell>
                <Table.Cell>{snSUHD}</Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>LTV</Table.Cell>
                <Table.Cell>UHD</Table.Cell>
                <Table.Cell>{lgUHD}</Table.Cell>
                <Table.Cell>{ssUHD}</Table.Cell>
                <Table.Cell>{snUHD}</Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>LTV</Table.Cell>
                <Table.Cell>FHD</Table.Cell>
                <Table.Cell>{lgFHD}</Table.Cell>
                <Table.Cell>{ssFHD}</Table.Cell>
                <Table.Cell>{snFHD}</Table.Cell>
              </Table.Row>
            </Table.Body>
          </Table>
        ) : null}
        {activo === "ATA" ? (
          <Table collapsing color="red">
            <Table.Header collapsing>
              <Table.HeaderCell>PL1</Table.HeaderCell>
              <Table.HeaderCell>Modelo</Table.HeaderCell>
              <Table.HeaderCell>Inch</Table.HeaderCell>
              <Table.HeaderCell>Year</Table.HeaderCell>
              <Table.HeaderCell>Competitor Model</Table.HeaderCell>
              <Table.HeaderCell>Year</Table.HeaderCell>
              <Table.HeaderCell>Promo LG</Table.HeaderCell>
              <Table.HeaderCell>TAG LG</Table.HeaderCell>
              <Table.HeaderCell>Promo Comp</Table.HeaderCell>
              <Table.HeaderCell>TAG Comp</Table.HeaderCell>
              <Table.HeaderCell textAlign="right">ATA</Table.HeaderCell>
            </Table.Header>
            <Table.Body>{atarows}</Table.Body>
          </Table>
        ) : null}
      </Segment>
    </Container>
  );
}

export default App;
